import React from 'react';
import Swiper from 'react-id-swiper';

import 'swiper/css/swiper.css';

import story1 from './../img/story1.svg';
import story2 from './../img/story2.svg';
import story3 from './../img/story3.svg';
import story4 from './../img/story4.svg';
import story5 from './../img/story5.svg';
import story6 from './../img/story6.svg';
import story7 from './../img/story7.svg';
import story8 from './../img/story8.svg';

const images = [
  story1, story2, story3, story4, story5, story6, story7, story8
];

function StorySlider() {
  const params = {
    width: 400,
    spaceBetween: 30,
    centeredSlides: true,
    autoplay: {
      delay: 2000,
      disableOnInteraction: false
    },
    pagination: {
      el: '.swiper-pagination',
      clickable: true
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev'
    }
  };
  return (
    <div>
      <Swiper { ...params }>
        { images.map( img => {
          return <img key={ img } src={ img } alt="story" />;
        } ) }
      </Swiper>
      </div>
  );
};

export default StorySlider;